.app-wrapper {
  display: grid;
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  grid-template-areas:
          "h h"
          "n c";
  width: 1200px;
  margin: 0 auto;
}

.app-wrapper-content {
  grid-area: c;
  background-color: cadetblue;
}

.app-wrapper-content img {
  width: 500px;
}